import React from 'react';
import CardItem from './CardItem';
import './Cards.css';
import { Link } from 'react-router-dom';
import { Button } from './Button'
import portf1 from '../images/portfolio2.png';
import myTunes from '../images/myTunes.png';
import medApp from '../images/meditationApp.png';
import portf2 from '../images/portfolio1.png';
import pata from '../images/patatap.png';

function Cards() {
  return (
    <div className='cards'>
      <h1 className='inner-heading'>Check out some of my projects</h1>
      <div className='cards__container'>
        <div className='cards__wrapper'>
        <ul className='cards__items'>
            <CardItem
              src={portf1}
              text='Responsive Portfolio Template'
              label='Portfolio Site'
              path='https://relaxed-lalande-8abdd0.netlify.app/' target='_blank'
            />
            <CardItem
              src={myTunes}
              text='ITunes Mock Landing Page'
              label='UX/UI Design'
              path='https://sad-borg-42321b.netlify.app/'
            />
          </ul>
          <ul className='cards__items'>
            <CardItem
              src={medApp}
              text='Meditation App JS Project'
              label='Meditation App'
              path='https://jovial-brown-01fa7d.netlify.app/'
            />
            <CardItem
              src={portf2}
              text='Responsive Single-Page Portfolio'
              label='Portfolio Site'
              path='https://goofy-bartik-11769d.netlify.app/'
            />
            <CardItem
              src={pata}
              text='Keyboard Sounds JS Project'
              label='Patatap Clone'
              path='https://optimistic-dijkstra-a9bbab.netlify.app/'
            />
          </ul>
        </div>
        <div className='about-b'>
            <h1 className='inner-heading'>
              Contact me for more info
            </h1>
            <Link to='/contact'>
            <Button 
              className='btns' 
              buttonSize='btn--medium'
              buttonStyle='btn--dark' 
              >
              Contact
            </Button>
            </Link>
            </div>
      </div>
    </div>
  );
}

export default Cards;
